import { Form, Input } from "antd";
import axios from "axios";
import React from "react";
import toast from "react-hot-toast";
import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { HideLoading, ShowLoading } from "../redux/alerts";

function StudentLogin() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const onFinish = async (values) => {
    try {
        
        dispatch(ShowLoading());
        const response = await axios.post(
          "/api/student/studentlogin", values);
         console.log(response.data);
        if (response.data.success) {
            dispatch(HideLoading());
          // localStorage.setItem("token", response.data.data);
          toast.success(response.data.message);
          console.log(response.data);
          navigate("/students/home");
        } else {
          toast.error(response.data.message);
        }
      } catch (error) {
        dispatch(HideLoading());
        toast.error(error.message);  
    }
  };
  return (
    <div className="login-page-1">
      <div className="row">
        <div className="col-md-6 d-flex align-items-center justify-content-center">
          <div className="text-part d-flex flex-column ">
            <Form layout="vertical w-400 p-4" onFinish={onFinish}>
              <h1 className="text-medium">Student Login</h1>
              <hr />
              <Form.Item
                
                name="rollNo"
                label="Roll Number"
                rules={[
                  {
                    required: true,
                    message: "Please enter your Roll No",
                  },
                ]}
              >
                <Input type="text" placeholder="Roll No"/>
              </Form.Item>

              <Form.Item
                name="password"
                label="Password"
                rules={[
                  {
                    required: true,
                    message: "Please enter your password",
                  },
                ]}
              >
                <Input type="password" placeholder="Password"/>
              </Form.Item>

              <button className=" text-white px-5 my-2 w-100">
                Login
              </button>
              Not Registered ?{" "}
              <Link to="/register" className="text-primary">
                 Click Here To Register
              </Link>
            </Form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default StudentLogin;
