import { Form, Input, Radio, Checkbox } from "antd";
import React from "react";
import { Link } from "react-router-dom";

function Register() {
  const onFinish = (values) => {
    console.log("Received values:", values);
    // Add your registration logic here
  };

  return (
    <div className="login-page-1">
      <div className="row">
        <div className="col-md-6 d-flex align-items-center justify-content-center">
          <div className="text-part d-flex flex-column">
            <Form layout="vertical w-400 white py-2 px-4" onFinish={onFinish}>
              <h1 className="text-medium">Student Registration</h1>
              <hr />
              <Form.Item
                name="name"
                label="Name"
                rules={[
                  {
                    required: true,
                    message: "Please enter your name",
                  },
                ]}
              >
                <Input />
              </Form.Item>
              <Form.Item
                name="rollno"
                label="Student Roll No."
                rules={[
                  {
                    required: true,
                    message: "Please enter your Roll Number",
                  },
                ]}
              >
                <Input />
              </Form.Item>

              <Form.Item
                name="class"
                label="Class"
                rules={[
                  {
                    required: true,
                    message: "Please select your class",
                  },
                ]}
              >
                <Radio.Group>
                  <Radio value="10">10th Grade</Radio>
                  <Radio value="11">11th Grade</Radio>
                  <Radio value="12">12th Grade</Radio>
                </Radio.Group>
              </Form.Item>
              <Form.Item
                name="subjects"
                label="Subjects"
                rules={[
                  {
                    required: true,
                    message: "Please select the subjects",
                  },
                ]}
              >
                <Checkbox.Group>
                  <Checkbox value="Mathematics">Mathematics</Checkbox>
                  <Checkbox value="Science">Science</Checkbox>
                  <Checkbox value="History">History</Checkbox>
                </Checkbox.Group>
              </Form.Item>
              <Form.Item
                name="password"
                label="Password"
                rules={[
                  {
                    required: true,
                    message: "Please enter your password",
                  },
                ]}
              >
                <Input.Password />
              </Form.Item>

              <Form.Item
                name="confirmPassword"
                label="Confirm Password"
                dependencies={["password"]}
                rules={[
                  {
                    required: true,
                    message: "Please confirm your password",
                  },
                  ({ getFieldValue }) => ({
                    validator(_, value) {
                      if (!value || getFieldValue("password") === value) {
                        return Promise.resolve();
                      }
                      return Promise.reject(
                        new Error("The two passwords do not match")
                      );
                    },
                  }),
                ]}
              >
                <Input.Password />
              </Form.Item>

              <button className="primary text-white px-5 my-2 w-100">
                REGISTER
              </button>
            </Form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Register;
