import { Col, Form, Row } from 'antd';
import React from 'react';


const ClassesForm = () => {
  return (
    <div>
      <Form layout="vertical"  >
        <Row gutter={[10, 10]}>
            <Form.Item
              label="Class Code"
              name="classCode"
              rules={[{ required: true, message: 'Please enter the Class Code' }]}
            >
              <input type="text" />
            </Form.Item>
          </Row>
          <h3>Subjects List</h3>
          <div className='d-flex flex-grow-1 align-items-center justify-content-space-around'>
            
          </div>
          <Row gutter={[10, 10]}>
            <Form.Item
              label="Subject Code"
              name="subjectCode"
              rules={[{ required: true, message: 'Please enter the Class Code' }]}
            >
              <input type="text" />
            </Form.Item>
        </Row>
        </Form>
    </div>
  )
}

export default ClassesForm
